import React from 'react';


const Solar = () => {
    return (
        <div>
            <div className="pagetitle">Our Wonderful Solar System!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2016/07/09/10/59/sun-1506019_1280.jpg"></img>
            </div></center>
            <p>Our solar system consists of 8 planets (Mercury, Venus, Earth, 
                Mars, Jupiter, Saturn, Uranus, and Neptune). Pluto was considered
                a planet in our solar system at one time but was removed recently. 
                The planets in our solar system circle around the Sun, which is 
                located in the center of our solar system. The sun's mass is larger than
                the mass of all of the planets in our solar system combined.
            </p>
            </content>
        </div>
    );
}

export default Solar;