import React from 'react';


const Saturn = () => {
    return (
        <div>
            <div className="pagetitle">Saturn!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2020/02/22/08/15/space-4869815_1280.jpg"></img>
            </div></center>
            <p>Saturn is one of the few planets that has rings in our solar system. Saturn has several rings. While not the largest planet in our solar system, Saturn is 
        still much larger than Earth. Saturn, like Jupiter, has many moons.
            </p>
            </content>
        </div>
    );
}

export default Saturn;