import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import DOMPurify from 'dompurify';

const Nav = () => {

    const [sideMenu, setSideMenu] = useState([]);
    const [openMenu, setOpenMenu] = useState(false);
    const [closeBtn, setCloseBtn] = useState([]);

    const setMenu = () => {
        var theMenu = [];
        if (!openMenu) {
             var theMenu = ["Mercury", "Venus", "Earth", "Mars", "Jupiter", "Saturn", "Uranus", "Neptune"];
        }
        setOpenMenu(!openMenu);
        setSideMenu(theMenu);
    }

    const clearMenu = () => {
        setSideMenu([]);
    }

    return (
        <div className="navBar">
            <div className="homelink">
                <Link to="/Solar">
                    <img className="homeimage" src="https://cdn.pixabay.com/photo/2012/01/09/09/10/sun-11582_1280.jpg"></img>
                </Link>
            </div>
            <div className="navLinks">
                <Link className="individualLink" to="/Mercury">Mercury</Link>
                <Link className="individualLink" to="/Venus">Venus</Link>
                <Link className="individualLink" to="/Earth">Earth</Link>
                <Link className="individualLink" to="/Mars">Mars</Link>
                <Link className="individualLink" to="/Jupiter">Jupiter</Link>
                <Link className="individualLink" to="/Saturn">Saturn</Link>
                <Link className="individualLink" to="/Uranus">Uranus</Link>
                <Link className="individualLink" to="/Neptune">Neptune</Link>
            </div>
            <button className="menubar" onClick={() => setMenu()}>
                <div className="menulines"></div> 
                <div className="menulines"></div> 
                <div className="menulines"></div>
            </button>
            <div className="sideMenu">
            {sideMenu.map((obj) => (
                <div className="menuLink"><Link className="theLink" to={`/${obj}`}>{obj}</Link>
                <br></br></div>
                )
            )}
            </div>
        </div>
    );
}

export default Nav;