import './App.css';

// react router import
import { useState } from 'react';
import {BrowserRouter, Switch, Route } from 'react-router-dom';
import Earth from './components/Earth';
import Nav from './components/Nav';
import Solar from './components/Solar';
import Mercury from './components/Mercury';
import Venus from './components/Venus'; 
import Mars from './components/Mars';
import Jupiter from './components/Jupiter'; 
import Saturn from './components/Saturn'; 
import Uranus from './components/Uranus'; 
import Neptune from './components/Neptune'; 


function App() {
  const [cartList, setCartList] = useState([]);
  const [depts, setDepts] = useState([]);

  return (
    <div className="App">
      <BrowserRouter>
      <Nav />
      <Switch>
          <Route path="/Mercury" render={(props) => (
            <Mercury {...props} cartList={cartList} />
            )} />
          <Route path="/Mars" render={(props) => (
            <Mars {...props} cartList={cartList} />
            )} />
          <Route path="/Earth" render={(props) => (
            <Earth {...props} cartList={cartList} />
            )} />
          <Route path="/Venus" render={(props) => (
            <Venus {...props} cartList={cartList} />
            )} />
          <Route path="/Jupiter" render={(props) => (
            <Jupiter {...props} cartList={cartList} />
            )} />
          <Route path="/Saturn" render={(props) => (
            <Saturn {...props} cartList={cartList} />
            )} />
          <Route path="/Uranus" render={(props) => (
            <Uranus {...props} cartList={cartList} />
            )} />
          <Route path="/Neptune" render={(props) => (
            <Neptune {...props} cartList={cartList} />
            )} />
          <Route path="/" render={(props) => (
            <Solar {...props} depts={depts}  />
            )} />
        </Switch>
        </BrowserRouter>
    </div>
  );
}

export default App;
