import React from 'react';


const Venus = () => {
    return (
        <div>
            <div className="pagetitle">Venus!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2011/12/13/14/39/venus-11022_960_720.jpg"></img>
            </div></center>
            <p>Venus is the hottest planet in our solar system. The second closest planet to the sun in our solar system is venus. Venus also does
        not have any moons like some other planets in our solar system. 
            </p>
            </content>
        </div>
    );
}

export default Venus;