import React from 'react';


const Neptune = () => {
    return (
        <div>
            <div className="pagetitle">Neptune!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2012/11/28/09/17/neptune-67537_960_720.jpg"></img>
            </div></center>
            <p>Neptune is the farthest planet from the sun in our solar system. Neptune
            has rings and moons. Neptune is much larger than Earth, about four times larger.
            </p>
            </content>
        </div>
    );
}

export default Neptune;