import React from 'react';


const Mars = () => {
    return (
        <div>
            <div className="pagetitle">Mars!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2011/12/13/14/30/mars-11012_960_720.jpg"></img>
            </div></center>
            <p>Mars is the fourth planet in our solar system from the sun. Mars is much smaller than Earth. The average year on Mars is almost 
            twice as long as the average year on Earth. Mars' gravity is less than half of that of Earths.
            </p>
            </content>
        </div>
    );
}

export default Mars;