import React from 'react';


const Jupiter = () => {
    return (
        <div>
            <div className="pagetitle">Jupiter!</div>
            <content>
            <center><div class="planetimg">
                <img className="theimage" src="https://cdn.pixabay.com/photo/2020/02/22/08/15/space-4869815_1280.jpg"></img>
            </div></center>
            <p>Jupiter is the fifth furthest planet from the sun in our solar system. Jupiter is the largest planet in our solar system in size. An incredible fact
        about Jupiter is that it has over 70 moons.
            </p>
            </content>
        </div>
    );
}

export default Jupiter;